
 .scrollToTopButton {

  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 99;
  width: 60px;
  height: 60px;
  width: 65px;
  height: 65px;
  background-color: transparent;
  cursor: pointer;
  transition: opacity 0.3s ease-in-out;
  border: none;
}

.scrollToTopButton:hover {
  opacity: 0.7;
}

