.loader {
  --background: linear-gradient(135deg, #23c4f8, #275efe);
  --shadow: rgba(39, 94, 254, .28);
  --text: #6c7486;
  --page: rgba(255, 255, 255, .36);
  --page-fold: rgba(255, 255, 255, .52);
  --duration: 3s;
  width: 200px;
  height: 140px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:before, .loader:after {
  --r: -6deg;
  content: "";
  width: 120px;
  box-shadow: 0 16px 12px var(--shadow);
  transform: rotate(var(--r) );
  position: absolute;
  top: 80%;
  bottom: 8px;
}

.loader:before {
  left: 4px;
}

.loader:after {
  --r: 6deg;
  right: 4px;
}

.loader div {
  width: 100%;
  height: 100%;
  z-index: 1;
  perspective: 600px;
  box-shadow: 0 4px 6px var(--shadow);
  background-image: var(--background);
  border-radius: 13px;
  position: relative;
}

.loader div ul {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
}

.loader div ul li {
  --r: 180deg;
  --o: 0;
  --c: var(--page);
  transform-origin: 100%;
  color: var(--c);
  opacity: var(--o);
  transform: rotateY(var(--r) );
  -webkit-animation: var(--duration) ease infinite;
  animation: var(--duration) ease infinite;
  position: absolute;
  top: 10px;
  left: 10px;
}

.loader div ul li:nth-child(2) {
  --c: var(--page-fold);
  animation-name: page-2;
}

.loader div ul li:nth-child(3) {
  --c: var(--page-fold);
  animation-name: page-3;
}

.loader div ul li:nth-child(4) {
  --c: var(--page-fold);
  animation-name: page-4;
}

.loader div ul li:nth-child(5) {
  --c: var(--page-fold);
  animation-name: page-5;
}

.loader div ul li svg {
  width: 90px;
  height: 120px;
  display: block;
}

.loader div ul li:first-child {
  --r: 0deg;
  --o: 1;
}

.loader div ul li:last-child {
  --o: 1;
}

.loader span {
  text-align: center;
  color: var(--text);
  margin-top: 20px;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
}

@keyframes page-2 {
  0% {
    opacity: 0;
    transform: rotateY(180deg);
  }

  20% {
    opacity: 1;
  }

  35%, 100% {
    opacity: 0;
  }

  50%, 100% {
    transform: rotateY(0deg);
  }
}

@keyframes page-3 {
  15% {
    opacity: 0;
    transform: rotateY(180deg);
